<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn to="/environments/save"><v-icon>mdi-plus</v-icon> New</v-btn>
        <v-simple-table>
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">Name</th>
              <th class="text-left">Region</th>
              <th class="text-right" style="width: 160px;"></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>{{ item.name }}</td>
              <td>{{ item.region }}</td>
              <td style="text-align: right">
                <v-btn x-small @click="deploy(item.id)" style="margin-right: 5px;">Deploy</v-btn>
                <v-btn x-small :to="{path: '/environments/save', query: {id: item.id}}">Edit</v-btn>
              </td>
            </tr>
            <tr v-if="items.length === 0">
              <td colspan="3">No record found</td>
            </tr>
            </tbody>
          </template>
          <v-overlay :value="loading">Loading...</v-overlay>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      items: []
    }
  },
  async mounted() {
    await this.loadItems();
  },
  methods: {
    async loadItems() {
      this.loading = true;
      try {
        let response = await axios.get('/api/environments',{params: {project_id: this.$store.state.project.selected.id}});
        this.items = response.data.environments;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    },
    async deploy(id) {
      if (confirm('Are you sure?') === false) {
        return;
      }
      this.loading = true;
      try {
        await axios.post('/api/deployments',{
          project_id: this.$store.state.project.selected.id,
          environment_id: id
        });
        this.$router.push({path: '/deployments'});
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>
